.container{
    width: 300px;
}

.results{
    display: flex;
    flex-direction: row;
    justify-self: start;
    justify-content: space-between;
    text-align: center;
    width: 100%;
}
.results .result{
    flex: 70%;
}
.results .clear{
    flex: 30%;
} 
.results .clear > input{
    width: 60px;
}
.result{
    display: flex;
    flex-direction: column;

}

.items{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.items left{
    display: flex;
    flex-direction: column;
}
button,input{
    background-color: #626862;
    color: black;
    border: none;
    border-radius: 50%;
    font-size: x-large;
    padding: 15px;
    margin: 10px;
}